/*
-----------------------------------------------------------------------------------------------------------
RCU - Initiation Info
-----------------------------------------------------------------------------------------------------------
*/
export const updateRcuInitiationInfoObj = (data: any, addressData: any): IRcuInitiationInfo => {
  const ccEmail = JSON.parse(data?.ccEmail ? data?.ccEmail : "{}");

  const structureAddressDataForTable = {
    address: addressData?.address ?? "",
    smAddressType: addressData?.propertyType ?? "",
    pincode: { pinCode: addressData?.pincode?.pinCode ?? "", uid: addressData?.pincode?.uid },
    city: { name: addressData?.city?.name ?? "", uid: addressData?.city?.uid },
    state: addressData?.city?.state?.name ?? "",
    primaryAddressType: addressData?.primaryAddressType ?? "",
  };

  const obj: IRcuInitiationInfo = {
    dateOfInitiation: data?.stage?.createdAtSystemDate,
    internalAssignee: { name: data?.stage?.assignee?.name },
    CCEmails: Array.isArray(ccEmail) ? ccEmail : [],
    description: data?.stage?.remarks,
    rcuType: data?.stage?.smStageType,
    externalVendor: { name: data?.stage?.partner?.name },
    profileCheckTable: [structureAddressDataForTable],
    stageDocuments: data?.stage?.stageDocuments
      ? data?.stage?.stageDocuments?.filter(
          (document: any) => document?.isSharedAtInitiation === true
        )
      : [],
  };
  return obj;
};

/*
-----------------------------------------------------------------------------------------------------------
Loan Summary
-----------------------------------------------------------------------------------------------------------
*/
export const updateRcuLoanSummaryObj = (data: any): IRcuLoanSummaryPageObj => {
  const obj: IRcuLoanSummaryPageObj = {
    rcuUid: data?.stage?.rcu?.uid,
    loanNo: data?.stage?.rcu?.loan?.lanId ?? "",
    branch: data?.stage?.rcu?.loan?.branch?.name ?? "",
    salesManager: data?.stage?.rcu?.loan?.salesManager?.name ?? "",
    creditManager: data?.stage?.rcu?.loan?.creditManager ?? "",
    agency: data?.stage?.partner?.name ?? "",
    dateOfReport: data?.stage?.rcu?.dateOfReportingSampling
      ? new Date(data?.stage?.rcu?.dateOfReportingSampling)
      : "",
    dedupeCheck: data?.stage?.rcu?.isDedupeCheckDone ? YES_NO.YES : YES_NO.NO,
    comments: data?.stage?.rcu?.dedupeDescription ?? "",
    commentsErrorMessage: "",
  };

  return obj;
};

/*
-----------------------------------------------------------------------------------------------------------
Applicant Details
-----------------------------------------------------------------------------------------------------------
*/
export const updateRcuApplicantDetailsObj = (
  data: any,
  coApplicantList: IRcuCoApplicantDetailsRowData[],
  addressData: any
): IRcuApplicantDetailsPageObj => {
  const prepareAddressInfo = {
    address: addressData?.address ?? "",
    smAddressType: addressData?.propertyType ?? "",
    pincode: {
      pinCode: addressData?.pincode?.pinCode ?? "",
      uid: addressData?.pincode?.uid,
    },
    city: {
      name: addressData?.city?.name ?? "",
      uid: addressData?.city?.uid,
    },
    state: addressData?.city?.state?.name ?? "",
    latitude: addressData?.latitude ? `${parseFloat(addressData.latitude).toFixed(4)}` : "",
    longitude: addressData?.longitude ? `${parseFloat(addressData?.longitude).toFixed(4)}` : "",
    uid: addressData?.uid ?? "",
    primaryAddressType: addressData?.primaryAddressType ?? "",
    propertyType: addressData?.propertyType ?? "",
  };

  const pageObj: IRcuApplicantDetailsPageObj = {
    firstName: data?.stage?.applicant?.firstName ?? "",
    middleName: data?.stage?.applicant?.middleName ?? "",
    lastName: data?.stage?.applicant?.lastName ?? "",
    mobileNumber: data?.stage?.applicant?.mobileNo ?? "",
    coApplicantDetailsTable: [...coApplicantList],
    showAddressOfType: addressData?.primaryAddressType
      ? addressData?.primaryAddressType
      : addressData?.smAddressType
      ? addressData?.smAddressType
      : "COLLATERAL_PROPERTY",
    addressData: prepareAddressInfo,
    nameOfOrganization: "",
  };

  return pageObj;
};

/*
-----------------------------------------------------------------------------------------------------------
Added Documents
-----------------------------------------------------------------------------------------------------------
*/
export const updatedRcuAddedDocumentsObj = (data: any): IRcuSingleAddedDocumentsPageObj[] => {
  const prepareDocumentSectionStructure = (sections: any) => {
    const preparedStructure: ISavedDocumentSections[] = sections.map((docSection: any) => ({
      uid: docSection.uid,
      fileName: docSection.fileName,
      fileType: docSection.fileType,
      section: docSection.section,
      sectionName: docSection.sectionName,
      thumbUrl: docSection.thumbUrl,
      url: docSection.url,
      isActive: docSection.isActive,
    }));

    return preparedStructure.length
      ? preparedStructure
      : [
          {
            uid: "",
            fileName: "",
            fileType: "",
            section: "",
            sectionName: "",
            thumbUrl: "",
            url: "",
            isActive: false,
          },
        ];
  };

  const structuredSavedDocument = (data: any): ISavedDocumentRecords[] => {
    const docStructureForDocUploader = {
      uid: data.uid,
      attributes: {
        ...data.attributes,
      },
      docCategory: data.docCategory,
      docType: { ...data.docType },
      documentSections: prepareDocumentSectionStructure(data.documentSections),
      loanSectionKey: data.loanSectionKey,
      isActive: data.isActive,
    };

    return [docStructureForDocUploader];
  };

  const obj = data.map((data: any) => {
    return {
      sectionMode: PAGE_MODE.VIEW,
      uid: data.uid,
      savedDocuments: structuredSavedDocument(data),
      status: data.smDocVerificationAction,
      remark: data.remarks,
      disable: false,
    };
  });

  return obj;
};

