/*
-----------------------------------------------------------------------------------------------------------
Rcu Upload Report
-----------------------------------------------------------------------------------------------------------
*/
export const updateRcuUploadReportObj = (data: any): IRcuUploadReportPageObj => {
  const prepareDocumentSectionStructure = (sections: any) => {
    const preparedStructure: ISavedDocumentSections[] = sections.map((docSection: any) => ({
      uid: docSection.uid,
      fileName: docSection.fileName,
      fileType: docSection.fileType,
      section: docSection.section,
      sectionName: docSection.sectionName,
      thumbUrl: docSection.thumbUrl,
      url: docSection.url,
      isActive: docSection.isActive,
    }));

    return preparedStructure.length
      ? preparedStructure
      : [
          {
            uid: "",
            fileName: "",
            fileType: "",
            section: "",
            sectionName: "",
            thumbUrl: "",
            url: "",
            isActive: false,
          },
        ];
  };

  const structLoanDocumentsFromAPIResponse = (documents: any[]) => {
    const structuredSavedDocument: ISavedDocumentRecords[] = documents?.map((doc: any) => ({
      uid: doc.uid,
      attributes: {
        ...doc.attributes,
      },
      docCategory: doc.docCategory,
      docFor: doc.docFor,
      docType: { ...doc.docType },
      documentSections: prepareDocumentSectionStructure(doc.documentSections),
      loanSectionKey: doc.loanSectionKey ?? "",
      isActive: doc.isActive,
    }));
    return structuredSavedDocument;
  };

  const obj: IRcuUploadReportPageObj = {
    savedDocuments: [...structLoanDocumentsFromAPIResponse(data?.loanDocuments ?? [])],
  };

  return obj;
};

