import AclGuard from "components/AclGuard";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import RichTextEditor from "components/RichTextEditor/RichTextEditor";
import SaveComponent from "components/Save/SaveComponent";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import ToastCustom from "components/Toast";
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react";
import RcuService from "services/transaction/DDE/RcuService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import { PAGE_MODE, STATIC_MASTER_TYPE_TOAST_TEXT, TOAST_STYLE_TYPE } from "utils/Constant";
import { IRcuDropdownList } from "../../PageReport/PageReport";

type TRcutTextEditorSection = "RCU_STATUS" | "PROFILE_VERIFICATION";
interface IProps {
  isLoading: boolean;
  data: IRcuTextEditorPageObj;
  getDataOnCancel: () => void;
  section: TRcutTextEditorSection;
  dropdownOptions?: IRcuDropdownList;
}

export interface IRcuTextEditorPageObj {
  rcuUid: string;
  rcuStatus: string;
  editorText: string;
  errorMessage: string;
}

export const initialRcuTextEditorPageObj: IRcuTextEditorPageObj = {
  rcuUid: "",
  rcuStatus: "",
  editorText: "",
  errorMessage: "",
};

interface IEditorRef {
  editor: {
    getContent: () => void;
    plugins: {
      wordcount: {
        body: {
          getCharacterCount: () => any;
        };
      };
    };
  };
}

export const RCU_TEXT_EDITOR_SECTION: Record<
  TRcutTextEditorSection,
  { headerLabel: string; code: TRcutTextEditorSection }
> = {
  PROFILE_VERIFICATION: {
    headerLabel: "label.rcu.profileVerification*",
    code: "PROFILE_VERIFICATION",
  },
  RCU_STATUS: {
    headerLabel: "",
    code: "RCU_STATUS",
  },
};

const SaveComponentTextEditor: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading, getDataOnCancel, section, dropdownOptions } = props;
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */
  const [pageObj, setPageObj] = useState<IRcuTextEditorPageObj>({
    ...initialRcuTextEditorPageObj,
  });
  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // ref
  const toastMessageRef = useRef(null);
  const editorRef = useRef<IEditorRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  useImperativeHandle(ref, () => ({
    save: onSave,
    onCancel: onCancel,
  }));

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    setPageObj((prevObj) => ({
      ...prevObj,
      ...data,
    }));
  }, [data]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const handleEditorValidation = (e: any) => {
    if (validateEditor()) {
      if (pageObj?.errorMessage) {
        setPageObj((prevPageObj) => ({ ...prevPageObj, errorMessage: "" }));
      }
    }
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const validateEditor = () => {
    const wordcount = editorRef?.current?.editor?.plugins?.wordcount?.body.getCharacterCount();

    if (wordcount < 1) {
      setPageObj((prevState) => {
        return {
          ...prevState,
          errorMessage: "Mandatory",
        };
      });
      return false;
    }

    return true;
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onSave = async () => {
    if (validateForm() && validateEditor()) {
      try {
        if (section === RCU_TEXT_EDITOR_SECTION.PROFILE_VERIFICATION.code) {
          if (editorRef.current?.editor?.getContent()) {
            const payload = enrichForSaveProfileVerification();
            const res = await RcuService.updateProfileVerification(payload);
          }
        }

        if (section === RCU_TEXT_EDITOR_SECTION.RCU_STATUS.code) {
          if (editorRef.current?.editor?.getContent()) {
            const payload = enrichForSaveRcuStatus();
            const res = await RcuService.updateRcuStatus(payload);
          }
        }

        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        setPageMode(PAGE_MODE.VIEW);
      } catch (error) {
        setErrorMessage(parseStandardAPIErrorMessage(error));
      }
    }
  };

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onCancel = () => {
    getDataOnCancel?.();
    resetValidator();
    setPageMode(PAGE_MODE.VIEW);
  };

  const handleChange = (e: any) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      [e.target.name]: e.target.value,
    }));
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForSaveProfileVerification = () => {
    return {
      uid: data.rcuUid ? data.rcuUid : "",
      profileVerification: editorRef.current?.editor?.getContent(),
    };
  };

  const enrichForSaveRcuStatus = () => {
    return {
      uid: data.rcuUid ? data.rcuUid : "",
      smRcuStatus: rcuStatus,
      overallRemarks: editorRef.current?.editor?.getContent(),
    };
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -------------------------------------------------------
      Sectional Renders
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
    Main Renders
    -------------------------------------------------------
  */

  const { rcuStatus } = pageObj;
  return (
    <div>
      <SaveComponent
        title={RCU_TEXT_EDITOR_SECTION?.[section].headerLabel ?? ""}
        pageMode={pageMode}
        onSave={onSave}
        onCancel={onCancel}
        onEdit={onEdit}
        disabled={isLoading}
      >
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
            <div className="grid grid-cols-4 gap-3 ">
              {RCU_TEXT_EDITOR_SECTION?.[section].code ==
              RCU_TEXT_EDITOR_SECTION.RCU_STATUS.code ? (
                <div className="col-span-1">
                  <SimpleDropDown
                    label={"label.rcu.rcuStatus"}
                    placeholder="placeholder.legal.status*"
                    name="rcuStatus"
                    value={rcuStatus}
                    optionLabel="label"
                    optionValue="value"
                    options={dropdownOptions?.rcuStatus ?? []}
                    onChange={handleChange}
                    validation={simpleValidator?.current?.message(
                      `rcuStatus`,
                      rcuStatus,
                      "mandatory"
                    )}
                    disabled={pageMode === PAGE_MODE.VIEW}
                  />
                </div>
              ) : null}

              <div className="col-span-4">
                {RCU_TEXT_EDITOR_SECTION?.[section].code ==
                  RCU_TEXT_EDITOR_SECTION.RCU_STATUS.code && (
                  <div>
                    <Text className={"text-xs"} label={"label.rcu.rcuStatusRemarks"} />
                  </div>
                )}

                <RichTextEditor
                  ref={editorRef}
                  validation={{ errorMessage: pageObj.errorMessage }}
                  initialValue={pageObj.editorText}
                  onChange={handleEditorValidation}
                  height={"400x"}
                  disabled={pageMode === PAGE_MODE.VIEW}
                />
              </div>
            </div>
          </AclGuard>
        )}
      </SaveComponent>

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
});

export default SaveComponentTextEditor;
